<script>
export default {
  data() {
    return {
      data: {},
      loading: false,
      server_errors: {},
      provinces: [],
      districts: [],
      selectedDistrict: "",
      villages: [],
      selectedVillage: "",
      village_details: [],
      selectedVillageDetail: [],
      tab: 'tab-1',

      address: [],
      preview_list: [],
      image_list: [],
      image: [],
      //Map
      latlng: {
        lat: 0,
        lng: 0,
      },
      markers: [],
      currentPlace: null,
      markerOptions: {
        // eslint-disable-next-line global-require
        url: require("@coms/../../src/assets/pin1.svg"),
        size: {
          width: 35,
          height: 55,
          f: "px",
          b: "px",
        },
        scaledSize: {
          width: 35,
          height: 55,
          f: "px",
          b: "px",
        },
      },
      services: [],
      serviceSummary: {},
      statusSummary: {},
      invoices: [],
      invoiceSummary: {},
      //Pagination
      offset: 12,
      pagination: {},
      per_page: 12,
    };

  },
  methods: {
    fetchData() {
      this.$store.commit("Loading_State", true);
      this.$axios
          .get("customer/" + this.$route.params.id)
          .then((res) => {
            if (res.data.code === 200) {
              setTimeout(() => {
                this.$store.commit("Loading_State", false);
                this.data = res.data.data;
              }, 300);
            }
          })
          .catch((error) => {
            this.$store.commit("Loading_State", false);
            this.fetchData();
            if (error.response.status === 422) {
              let obj = error.response.data.errors;
              for (let [key, message] of Object.entries(obj)) {
                this.server_errors[key] = message[0];
              }
            }
          });
    },
    customerCollection() {
      // this.this.pagination.current_page = "";
      this.$store.commit("Loading_State", true);
      this.$axios
          .get("customer-collection-summary/" + this.$route.params.id, {
            params: {
              page: this.pagination.current_page,
              per_page: this.per_page,
            },
          })
          .then((res) => {
            if (res.data.code === 200) {
              setTimeout(() => {
                this.$store.commit("Loading_State", false);
                this.services = res.data.data.details.data;
                this.serviceSummary = res.data.data.collect_summary;
                this.statusSummary = res.data.data.status_summary;
                this.pagination = res.data.data.details.pagination;
              }, 300);
            }
          })
          .catch((error) => {
            this.$store.commit("Loading_State", false);
            this.fetchData();
            if (error.response.status === 422) {
              let obj = error.response.data.errors;
              for (let [key, message] of Object.entries(obj)) {
                this.server_errors[key] = message[0];
              }
            }
          });
    },
    customerInvoice() {
      // this.this.pagination.current_page = "";
      this.$store.commit("Loading_State", true);
      this.$axios
          .get("customer-invoice-summary/" + this.$route.params.id, {
            params:{
              page: this.pagination.current_page,
              per_page: this.per_page,
            }
          })
          .then((res) => {
            if (res.data.code === 200) {
              setTimeout(() => {
                this.$store.commit("Loading_State", false);
                this.invoices = res.data.data.details.data;
                this.invoiceSummary = res.data.data.invoice_summary;
                this.pagination = res.data.data.details.pagination;
              }, 300);
            }
          })
          .catch((error) => {
            this.$store.commit("Loading_State", false);
            this.fetchData();
            if (error.response.status === 422) {
              let obj = error.response.data.errors;
              for (let [key, message] of Object.entries(obj)) {
                this.server_errors[key] = message[0];
              }
            }
          });
    },
    backPrevios() {
      this.$router.go(-1);
    },
    getCenter() {
      if (this.data.lat) {
        const latlng = {
          lat: parseFloat(this.data.lat),
          lng: parseFloat(this.data.lng),
        };
        return latlng;
      }
      return this.latlng;
    },
    getMarkers(data) {
      return {
        lat: parseFloat(data.lat),
        lng: parseFloat(data.lng),
      };
    },
    editPage(id) {
      this.$router.push({
        name: "EditCustomer",
        params: {id},
      });
    },
  },
  watch: {
 tab:function (){
     if(this.tab === 'tab-1'){
       this.fetchData();
     } else
     if(this.tab === 'tab-2'){
       this.customerCollection();
     } else
     if(this.tab === 'tab-3'){
       this.customerInvoice();
     }

 }
  },
  created() {
    this.fetchData();
    // this.customerInvoice();
    // this.customerCollection();
  },
};
</script>

